import React,{Component} from 'react'
import './Login.css'
import {Link} from 'react-router-dom'
import connect from "react-redux/es/connect/connect";
import asyncUser from "../../store/actions/asyncUser";


import './Login.css'
class Login extends Component{
    render(){
        return(
            <>
            	<div id="register-b">
                    
                    <nav className="j-nav navbar">
                        <div className="logo fl">
                            <Link to='/home'></Link>
                        </div>
                        <span className="user-title">登录</span>
                        <div className="registerBtn">
                            <Link to='/reg'>注册</Link>
                        </div>
                    </nav>
                    <div className="logina">
                        <form>
                            <ul>
                                <li>
                                    <img src="/images/login.png"/>
                                    <label>账号</label>
                                    <input type="text" placeholder="请输入账号" name="username" defaultValue={this.state.username} onClick={this.changeIpt.bind(this)} />
                                </li>
                                <li>
                                    <img src="/images/password.png" />
                                    <label>密码</label>
                                    <input type="password" placeholder="请输入密码" name="password" defaultValue={this.state.password} onChange={(ev)=>this.changeIpt(ev)}/>
                                </li>
                            </ul> 
                            <input type="button" value="登录" onClick={
                                this.props.login.bind(null,this.state.username,this.state.password,this.props.history)
                                } />
                        </form>
                    
                    </div>
                </div>
            </>
        )

    }
    state={
        username:"",
        password:""
    }
    changeIpt(ev){
        this.setState({
            [ev.target.name]:ev.target.value
        })
    }
    // login(){
    //     console.log("login",this.state.username,this.state.password)
    //     fetch(
    //         `/data/user.json`
    //     ).then(
    //         res=>res.json()
    //     ).then(
    //         data=>{
    //             if(data.auth){
    //                 this.props.history.push({pathname:"/user"})
    //             }else{
    //                 this.props.history.push({pathname:"/error"})
    //             }
    //         }
    //     )
    // }
}

const initMapStateToProps=state=>({
	// username:state.user.data.username
  });
  const initMapDispatchToProps=dispatch=>({
    login:(username,password,history)=>dispatch(asyncUser({
      url:'data/user.json',
      username,password
    })).then(
      (auth)=>auth ?
        history.push({pathname:'/user'}) :
        history.push({pathname:'/login'})
    )
  });
  export default connect(
    initMapStateToProps,
    initMapDispatchToProps
  )(Login);