import React,{Component} from 'react';
// import querystring from 'query-string'
import {Link} from 'react-router-dom';
// import ToDoList from '../../components/ToDoList/ToDoList';

class Detail extends Component{
    state={
        detail:[]
    };
    componentDidMount(){
        let id=this.props.match.params.id-0;
        // let dataName=querystring.parse(this.props.location.search).dataName;
        fetch(
            `/data/follow.data`
        ).then(
            res=>res.json()
        ).then(
            data=>this.setState({
                detail:data[id-1] 
                  
            })
        )
    };
    render(){
        let detail =this.state.detail;
        console.log(detail)
        return (
            <>
                    <div data-am-widget="slider" className="am-slider am-slider-a1" data-am-slider='{"directionNav":false}' >
	    <ul className="am-slides cl">
	        <li>
	        	<img src={detail.img} alt= ""/>
	        </li>
	        <li>
	        	<img src={detail.img} alt= ""/>
	        </li>
	    </ul>
	</div>
    <div className="details-body-box">
        <div className="tm">
            <div className="td2">
                <div className="td2_price"><span>已售：<b>948</b>件</span></div>
                <div className="cl"></div>
                <div className="td2_name"> {detail.title} </div>
                <div className="td2_cx">{detail.miaoshu}</div>
            </div>
        </div>
        <div className="tm2">
            <div className="td2_info">
                <div className="td2_num">支付开团并邀请<span id="tuan_more_need_number">{detail.num}</span>人参团，人数不足自动退款</div>
            </div>
            <div style={{visibility: 'visible'}} className="kt">
                <div className="pin"> 
                    <div className="pin_btn_box">
                        <div className="pin_btn">
                           {detail.people}
                        </div>
                    </div>
                    <div className="pin_btn_box">
                        <div className="pin_btn pin_btn_bg2">
                            原价
                        </div>
                    </div>
                    <div className="cl"></div>
                </div>
                <div>
                    <a href=" " className="kt_item kt_item_buy ">
                        <div className="kt_price"><b>{detail.oldprice}</b>&nbsp;/&nbsp;{detail.ke}</div>
                        <div className="kt_btn" id="tuan_one_number">原价购买</div>
                    </a>
                    <a href="pay.html" className="kt_item">
                        <div className="kt_price"><b>{detail.price}</b>&nbsp;/&nbsp;{detail.ke}</div>
                        <div className="kt_btn">立即拼团<br/></div>
                    </a>   
                </div>
            </div> 
        </div>       
        <div className="step">
            <div className="step_hd">
                拼团玩法<a className="step_more" href="tuan_rule.html">查看详情</a>
            </div>
            <div id="footItem" className="step_list">
                <div className="step_item step_item_on">
                    <div className="step_num">1</div>
                    <div className="step_detail">
                        <p className="step_tit">选择
                            <br/>心仪商品</p>
                    </div>
                </div>
                <div className="step_item ">
                    <div className="step_num">2</div>
                    <div className="step_detail">
                        <p className="step_tit">支付开团
                            <br/>或参团</p>
                    </div>
                </div>
                <div className="step_item ">
                    <div className="step_num">3</div>
                    <div className="step_detail">
                        <p className="step_tit">等待好友
                            <br/>参团支付</p>
                    </div>
                </div>
                <div className="step_item">
                    <div className="step_num">4</div>
                    <div className="step_detail">
                        <p className="step_tit">达到人数
                            <br/>团购成功</p>
                    </div>
                </div>
            </div>
        </div>
        <div className="outs-box">
            <h1>商品详情</h1>
            <img src="/images/d1.jpg" alt= ""/>
            <img src="/images/d2.jpg" alt= ""/>
            <img src="/images/d3.jpg" alt= ""/>
            <img src="/images/d4.jpg" alt= ""/>
        </div>
    </div>

	<footer className="detail_footer">
        <div className="return_left_box">
            <Link to={{pathname:"/home"}}><div className="return_home fl"></div></Link>
            <a href=" "><div className="return_home share_tuan fl"></div></a>    
        </div>  
        <a href="pay.html"><div className="promptly_tuan fl">立即去拼团</div></a>
        
        <div className="cl"></div>

    </footer>
            </>
        )
    }
}

export default Detail;