import React,{Component} from 'react';
import Home from './Home/Home'
import Header from '../common/Header/Header';
import Loading from '../common/Loading/Loading';

import Footer from '../common/Footer/Footer';
import Detail from './Detail/Detail';
// import Coupon from './Coupon/Coupon';
import Groups from './Groups/Groups';
// import Groupsdetails from './Groupsdetails/Groupsdetails';
import Myorder from './Myorder/Myorder';
import User from './User/User';
 import Reg from './Reg/Reg';
import Login from './Login/Login';
import Auth from '../guard/Auth';
import Error from '../common/Error/Error'


import {Route,Redirect,Switch} from 'react-router-dom';
// import pubsub from 'pubsub-js';

import {connect} from 'react-redux';
import * as types from "../store/types"

class App extends Component{
    componentWillReceiveProps(nextprops){
        let path=nextprops.location.pathname;

        this.watchRouter(path)
        
    }
    watchRouter(path){
        let {viewNav,viewFoot}=this.props;
        console.log(path)
        if(/home/.test(path)){
            // this.setState({
            //     bNav:true,
            //     bFoot:true
            // })
            viewNav(true);
            viewFoot(true)

        }
        if(/groups|myorder|user/.test(path)){
            // this.setState({
            //     bNav:false,
            //     bFoot:true
            // })
            viewNav(false);
            viewFoot(true)
        }
        if(/detail|login|reg/.test(path)){
            // this.setState({
            //     bNav:false,
            //     bFoot:false
            // })
            viewNav(false);
            viewFoot(false)
        }
    }
        componentDidMount(){
        let path=this.props.location.pathname;
            
        this.watchRouter(path)
        }
    // }
    render(){
        let {bNav,bFoot,bLoading} =this.props;
        console.log(bNav,bFoot,bLoading)
        return (
            <>
            {bLoading && <Loading/>}
                {bNav && <Header/>}
                <Switch>
                    <Route path='/home' component={Home} />
                    <Route path='/groups' component={Groups} />
                    <Route path='/myorder' component={Myorder} />
                    {/* <Route path='/user' component={User} /> */}
                    <Auth path='/user' component={User} />

                    <Route path='/login' component={Login} />
                    <Route path='/reg' component={Reg} />
                    <Route path='/detail/:id' component={Detail} />
                    <Redirect exact from="/" to="/home" />
                    <Route component={Error} />  
                </Switch>              
                { bFoot && <Footer/>}
            </>
        )
    }
}

const initMapStateToProps=state=>({
    bNav:state.bNav,
    bFoot:state.bFoot,
    bLoading:state.bLoading

})
const initMapDispatchToProps=dispatch=>({
    viewNav:(bl)=>dispatch({type:types.VIEW_NAV,payload:bl}),
    viewFoot:(bl)=>dispatch({type:types.VIEW_FOOT,payload:bl}),

})

export default connect(
    initMapStateToProps,
    initMapDispatchToProps
)(App);