import React from 'react';

import {NavLink} from "react-router-dom";
const Footer =(props)=>(
    <div>
        <footer className="footer">
        <nav>
            <ul>
                <li>
                    <NavLink to={{pathname:"/home"}} className="nav-controller"><div className="fb-home"></div>首页</NavLink>
                    </li>
                <li><NavLink to={{pathname:"/groups"}} className="nav-controller"><div className="fb-group"></div>我的拼团</NavLink></li>
                <li><NavLink to={{pathname:"/myorder"}} className="nav-controller"><div className="fb-list"></div>我的订单</NavLink></li>
                <li><NavLink to={{pathname:"/user"}} className="nav-controller"><div className="fb-user"></div>个人中心</NavLink></li>
            </ul>
        </nav>
    </footer>
    </div>
);

export default Footer;