// 异步action
// 1. 先给外面得dispatch返回一个对象
// 2. 公共得异步处理业务
//3. 提交请求到的数据给reducer，同时也可以给调通方一个回执
import * as types from '../types'

//返回函数
const asyncList=({type,url,start,count,id})=>(dispatch,getState)=>{

    dispatch({type:types.VIEW_LOADING,payload:true});
     fetch(
        url,
        // body:{
        //     start
        //     count
        // }
    ).then(
        res=>res.json()
    ).then(
        data=>{
            dispatch({type:types.VIEW_LOADING,payload:false});
            if(id){
                dispatch({type:type,payload:{data,id}})
            }else{
                dispatch({type:type,payload:data})
            }
        }
    )

};

export default asyncList;